const shippingCost = 10;
export const cart = [];

// shippingCost and   cart are scoped to current module, so we can only use them here.
// gdybym chcia uy ich w script.js module, musiabym uy exports. W ES Modules s 2 typy exports: named exports and default exports.

// named export jest prostszy, wystarczy przed kodem, ktry chc exportowa, napisa export, jak poniej:
export const addToCart = function (product, quantity) {
  cart.push({ product, quantity });
  console.log(`${quantity} ${product} added to cart`);
};

const totalPrice = 237;
const totalQuantity = 23;

export { totalPrice, totalQuantity as qt }; // mog exportowa wiele rzeczy na raz, w taki sposb.

export default function (product, quantity) {
  cart.push({ product, quantity });
  console.log(`${quantity} ${product} added to cart`);
}
