// The module pattern
const ShoppingCart2 = (function () {
  // Przypisuj IIFE do zmiennej, bo z niej bd wywoywa zmienne i metody
  const cart = [];
  const shippingCost = 10;
  const totalPrice = 237;
  const totalQuantity = 23;

  const addToCart = function (product, quantity) {
    cart.push({ product, quantity });
    console.log(`${quantity} ${product} added to cart`);
  };
  const orderStock = function (product, quantity) {
    console.log(`${quantity} ${product} ordered from supplier`);
  };
  return {
    addToCart,
    cart,
    totalPrice,
    totalQuantity,
  };
})();
// Wszystkie powysze dane s prywatne, bo s wewntrz funkcji. Teraz po prostu musz zwroci niektre dane, aby uzyska public API. Czyli zwracam obiekt, z danymi, ktre chc upubliczni.

